import { Component, OnInit } from '@angular/core';
import { DadesService } from '../services/dades.service'

@Component({
  selector: 'app-pfizer',
  templateUrl: './pfizer.page.html',
  styleUrls: ['./pfizer.page.scss'],
})
export class PfizerPage implements OnInit {

    public vacunes: Array<any>;
    public dones: number = 0;
    public homes: number = 0;
    public numtotalP: number = 0;
    public dosiH: number = 0;
    public dosiD = 0;


  constructor(private dades: DadesService) { }

  ngOnInit() {
    this.dades. dadesVacunes().subscribe(
      (data: Array<any>) => {
        
        //Recollir dades i guardarles a les propietats totals
        this.vacunes = data;

        this.vacunes.forEach(element => {  
  
          if(element.fabricant == "BioNTech / Pfizer"){
            this.numtotalP = +this.numtotalP+ +element.recompte
            if(element.sexe_codi == "0"){
              this.homes = +this.homes+ +1
              this.dosiH = +this.dosiH + +element.dosi
              
            }else{
       
              this.dones = +this.dones+ +1
              this.dosiD = +this.dosiD + +element.dosi
              

            }
          }
      
        });
      }
    )
    }
   
}
